.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.todo-list-template {
    background: white;
    max-width:500px;
    width: 100%;
    box-shadow: 0 3px 10px rgba(0,0,0,0.16), 0 3px 50px rgba(0,0,0,0.23); /* 그림자 */ 
    margin: 0 auto; /* 페이지 중앙 정렬 */
  }
  
  .title {
    padding: 1rem;
    font-size: 1.5rem;
    text-align: center;
    font-weight: bold;
    background: #339af0;
    color: white;
  }
  
  .form-wrapper {
    padding: 1rem;
    border-bottom: 1px solid #f1f3f5;
  }
  
  .todos-wrapper {
    min-height: 5rem;
  }
.todo-item {
    padding: 1rem;
    display: flex;
    align-items: center; /* 세로 가운데 정렬 */
    cursor: pointer;
    -webkit-transition: all 0.15s;
    transition: all 0.15s;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
  }
  
  .todo-item:hover {
    background: #e3fafc;
  }
  
  /* todo-item 에 마우스가 있을때만 .remove 보이기 */
  /* .todo-item:hover .remove {
    opacity: 1;
  } */
  
  /* todo-item 사이에 윗 테두리 */
  .todo-item + .todo-item {
    border-top: 1px solid #f1f3f5;
  }
  
  
  .remove {
    width:1.5rem;
    height:1.5rem;
    margin-right: 1rem;
    color: #e64980;
    font-size:1.8rem;
    line-height: 1.3rem;
    font-weight: 600;
    /* opacity: 0; */
  }
  
  .todo-text {
    flex: 1 1; /* 체크, 엑스를 제외한 공간 다 채우기 */
    word-break: break-all;
  }

  .todo-text.color1{
    color:#343a40;
  }
  .todo-text.color2{
    color:#f03e3e;
  }
  .todo-text.color3{
    color:#12b886;
  }
  .todo-text.color4{
    color:#228ae6;
  }
  
  .todo-text.checked {
    text-decoration: line-through;
    color: #adb5bd;
  }
  
  .check-mark {
    font-size: 1.5rem;
    line-height: 1rem;
    margin-left: 1rem;
    color: #3bc9db;
    font-weight: 800;
  }
.form {
    display: flex;
  }
  
  .form input {
    font-size: 1.25rem;
    outline: none;
    border: none;
    border-bottom: 1px solid #d0ebff;
    width:75%;
  }

  .form input.color1{
    color:#343a40;
  }
  .form input.color2{
    color:#f03e3e;
  }
  .form input.color3{
    color:#12b886;
  }
  .form input.color4{
    color:#228ae6;
  }
  
  .create-button {
    width:25%;
    box-sizing: border-box;

    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    padding-left: 1rem;
    padding-right: 1rem;
    margin-left: 1rem;
    background: #1971c2;
    border-radius: 3px;
    color: white;
    font-weight: 600;
    cursor: pointer;
    text-align: center;
  }
  
  .create-button:hover {
    background: #a5d8ff;
  }
.palette {
    display: flex;
    justify-content: left;
    align-items: center;
  }
  
  .color {
    width: 1rem;
    height: 1rem; 
    opacity: 0.5;
    -webkit-transition: all 0.2s;
    transition: all 0.2s;
    cursor: pointer;
  }

  .palette-color1{
    background:#343a40;
  }
  .palette-color2{
    background:#f03e3e;
  }
  .palette-color3{
    background:#12b886;
  }
  .palette-color4{
    background:#228ae6;
  }
  
  .color:hover {
    opacity: 0.75;
  }
  
  .color.active, .color:active {
    opacity: 1;
  }
  
  .color + .color {
    margin-left: .5rem;
  }
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background: #f9f9f9;
}
